<script>
import appConfig from '@src/app.config'
import Layout from '@layouts/main'
import PageHeader from '@components/page-header'
import axios from 'axios';
import Utils from '../../../../utils/Util';

/**
 * Invoice component
 */
export default {
	page: {
		title: 'Menu',
	},
	components: { Layout, PageHeader },
	data() {
		return {
			title: 'Menu',
			api: Utils.Host(),
			message: "",
			data:[],
			options: "",
			alert:false,
			dataotoritaswebsite: [],
			modelotoritaswebsite:"",
			dataotoritas: [],
			modelotoritas:"",
			f_lihat:[],
			f_tambah:[],
			f_update:[],
			f_delete:[],
			dataakses: [ { "f_kode": "1", "f_nama": "Tidak" }, { "f_kode": "0", "f_nama": "Aktif"} ], 
			modelakseslihat:"",
		}
	},
	mounted() {
      console.log("alamat", Utils.Host())
      this.Getdata()
	  this.Getfilter()
    },
	computed: {
	  pilihmodelotoritaswebsite() {
        return this.Getdata(this.modelotoritaswebsite);
      },
	  pilihmodelotoritas() {
        return this.Getdata(this.modelotoritaswebsite, this.modelotoritas);
      },
    },
	methods: {
      async Getdata(otoritaswebsite, otoritas) {
        try {
			let kodeotoritaswebsite = otoritaswebsite ? otoritaswebsite.f_kode : "";
			let kodeotoritas = otoritas ? otoritas.f_otoritas_id : "";
			if(this.modelotoritas){
            let response = await axios.get(this.api+"/menu/index?otoritaswebsite=" +
            kodeotoritaswebsite+
			"&otoritas=" +
            kodeotoritas +"&otoritas_id=" +
							this.modelotoritas.f_otoritas_id, Utils.authHeader());
            this.data = response.data;
				for (const tampil of response.data) {
					// cek
					await axios.get(this.api+"/menuotoritas/index?menu_id=" +
					tampil.f_menu_id+
							"&otoritas_id=" +
							this.modelotoritas.f_otoritas_id, Utils.authHeader());

					// cek
				}
			}
        } catch (err) {
            console.error(err);
        }
      },
      async openEdit(id) {
          document.location = '/menuaction/edit/' + id
      },
	  async DataLihat(menu, otoritas, status){
		await axios.get(this.api+"/menuotoritas/editlihat?f_otoritas_id=" +
		otoritas+
			"&f_menu_id=" +
            menu +"&status=" +
			status, Utils.authHeader());
			this.alert = true
        	this.message = 'Berhasil Update'

	  },
	  async DataTambah(menu, otoritas, status){
		await axios.get(this.api+"/menuotoritas/edittambah?f_otoritas_id=" +
		otoritas+
			"&f_menu_id=" +
            menu +"&status=" +
			status, Utils.authHeader());
			this.alert = true
        	this.message = 'Berhasil Update'

	  },
	  async DataUpdate(menu, otoritas, status){
		await axios.get(this.api+"/menuotoritas/editupdate?f_otoritas_id=" +
		otoritas+
			"&f_menu_id=" +
            menu +"&status=" +
			status, Utils.authHeader());
			this.alert = true
        	this.message = 'Berhasil Update'

	  },
	  async DataDelete(menu, otoritas, status){
		await axios.get(this.api+"/menuotoritas/editdelete?f_otoritas_id=" +
		otoritas+
			"&f_menu_id=" +
            menu +"&status=" +
			status, Utils.authHeader());
			this.alert = true
        	this.message = 'Berhasil Update'

	  },
	  async DataLihatall(otoritas, status){
			await axios.get(this.api+"/menuotoritas/editlihat?f_otoritas_id=" +
			otoritas+
				"&status=" +
				status, Utils.authHeader());
				this.alert = true
        		this.message = 'Berhasil Update'

		},
		async DataTambahall(otoritas, status){
			await axios.get(this.api+"/menuotoritas/edittambah?f_otoritas_id=" +
			otoritas+
				"&status=" +
				status, Utils.authHeader());
				this.alert = true
        		this.message = 'Berhasil Update'

		},
		async DataUpdateall(otoritas, status){
			await axios.get(this.api+"/menuotoritas/editupdate?f_otoritas_id=" +
			otoritas+
				"&status=" +
				status, Utils.authHeader());
				this.alert = true
        		this.message = 'Berhasil Update'

		},
		async DataDeleteall(otoritas, status){
			await axios.get(this.api+"/menuotoritas/editdelete?f_otoritas_id=" +
			otoritas+
				"&status=" +
				status, Utils.authHeader());
				this.alert = true
        		this.message = 'Berhasil Update'

		},
      async openDetail(id) {
          document.location = '/menuaction/detail/' + id
      },
	  async Getfilter() {
        try {
			let response = await axios.get(this.api+"/otoritaswebsite/index", Utils.authHeader());
    		this.dataotoritaswebsite = response.data;
			let response2 = await axios.get(this.api+"/otoritas/index", Utils.authHeader());
    		this.dataotoritas = response2.data;
        } catch (err) {
            console.error(err);
        }
      },
      async openRemove(id) {
		if (confirm('Anda Yakin akan menghapus Data')) {
		const item = this.data.find((el) => el.f_menu_id === id);
        const res =  await axios.delete(this.api+"/menu/delete/"+item.f_menu_id, Utils.authHeader());
		this.alert = true
        this.message = res.data.message
        this.Getdata()
		} else {
        
		}
	  }
	}
}
</script>

<template>
	<Layout>
		<PageHeader :title="title" :items="items" class="d-print-none" />
		<div class="row">
			<div class="col-12">
				<div class="card">
					<div class="card-body">
						<!-- Logo & title -->
						
						<div class="row">
							<div class="col-12">
								<div class="table-responsive">
									
									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Nama Website"
										label-for="input-horizontal"
									>
									<b-form-select v-model="modelotoritaswebsite" @click="pilihmodelotoritaswebsite">
										<option v-for='(value, key) in dataotoritaswebsite' :value='value'>{{value.f_nama}}</option>
									</b-form-select>
									</b-form-group>	

									<b-form-group
										label-cols-sm="10"
										label-cols-lg="2"
										label="Otoritas"
										label-for="input-horizontal"
									>
									<b-form-select v-model="modelotoritas" @click="pilihmodelotoritas">
										<option v-for='(value, key) in dataotoritas' :value='value'>{{value.f_otoritas}} - {{value.fn_otoritas_website}}</option>
									</b-form-select>
									</b-form-group>	

									<br>
									<br>&nbsp;&nbsp;
									<b-button variant="dark" to="/menuaction/tambah">Tambah Data</b-button>
									<br>
									<b-alert
											v-model="alert"
											variant="danger"
											dismissible
											>{{ message }}</b-alert
										>
			
									<div class="table-responsive">
									<table class="table mb-0">
										<thead>
										<tr>
											<th scope="col">No</th>
											<th scope="col">Main Menu</th>
											<th scope="col">Sub Menu</th>
											<th scope="col">Kode</th>
											<th scope="col">Nama</th>
											<th scope="col">Link</th>
											<th scope="col">Nama Website</th>
											<th scope="col">Action</th>
											<th scope="col">LIHAT
												<b-form-select>
													<option v-for='(value, key) in dataakses' :value='value.f_kode'  @click="DataLihatall(modelotoritas.f_otoritas_id, value.f_kode)">{{value.f_nama}}</option>
												</b-form-select>
											</th>
											<th scope="col">TAMBAH
												<b-form-select>
													<option v-for='(value, key) in dataakses' :value='value.f_kode'  @click="DataTambahall(modelotoritas.f_otoritas_id, value.f_kode)">{{value.f_nama}}</option>
												</b-form-select>
											</th>
											<th scope="col">UPDATE
												<b-form-select>
													<option v-for='(value, key) in dataakses' :value='value.f_kode'  @click="DataUpdateall(modelotoritas.f_otoritas_id, value.f_kode)">{{value.f_nama}}</option>
												</b-form-select>
											</th>
											<th scope="col">DELETE
												<b-form-select>
													<option v-for='(value, key) in dataakses' :value='value.f_kode'  @click="DataDeleteall(modelotoritas.f_otoritas_id, value.f_kode)">{{value.f_nama}}</option>
												</b-form-select>
											</th>
										</tr>
										</thead>
										<tbody>
										<tr v-for="(tampil,index) in data" :key="tampil.f_menu_id">
											<th scope="row">{{index+1}}</th>
											<td>{{tampil.f_main}}</td>
											<td>{{tampil.f_sub}}</td>
											<td>{{tampil.f_kode}}</td>
											<td>{{tampil.f_nama}}</td>
											<td>{{tampil.f_link}}</td>
											<td>{{tampil.fn_otoritas_website}}</td>
											<td>
											<b-button variant="warning" @click="openEdit(tampil.f_menu_id)">Edit Data</b-button>
											&nbsp;
											<b-button variant="danger" @click="openRemove(tampil.f_menu_id)">Delete Data</b-button>
											</td>
											<td>
													<b-form-select v-model="tampil.f_lihat">
														<option v-for='(value, key) in dataakses' :value='value.f_kode'  @click="DataLihat(tampil.f_menu_id, modelotoritas.f_otoritas_id, value.f_kode)">{{value.f_nama}}</option>
													</b-form-select>
											</td>
											<td>
													<b-form-select v-model="tampil.f_tambah">
														<option v-for='(value, key) in dataakses' :value='value.f_kode'  @click="DataTambah(tampil.f_menu_id, modelotoritas.f_otoritas_id, value.f_kode)">{{value.f_nama}}</option>
													</b-form-select>
											</td>
											<td>
													<b-form-select v-model="tampil.f_update">
														<option v-for='(value, key) in dataakses' :value='value.f_kode'  @click="DataUpdate(tampil.f_menu_id, modelotoritas.f_otoritas_id, value.f_kode)">{{value.f_nama}}</option>
													</b-form-select>
											</td>
											<td>
													<b-form-select v-model="tampil.f_delete">
														<option v-for='(value, key) in dataakses' :value='value.f_kode'  @click="DataDelete(tampil.f_menu_id, modelotoritas.f_otoritas_id, value.f_kode)">{{value.f_nama}}</option>
													</b-form-select>
											</td>
										</tr>
										</tbody>
									</table>
									</div>

								</div>
								<!-- end table-responsive -->
							</div>
							<!-- end col -->
						</div>
						<!-- end row -->

	
					</div>
				</div>
			</div>
			<!-- end col -->
		</div>
		<!-- end row -->
		<!-- end row -->
	</Layout>
</template>
